Reset + bazowe style
*, *::before, *::after { box-sizing: border-box; }
html { hanging-punctuation: first last; }
body { margin: 0; font-family: system-ui, -apple-system, Segoe UI, Roboto, Ubuntu, Cantarell, Noto Sans, Arial, sans-serif;
  line-height: 1.6; color: var(--fg); background: var(--bg); -webkit-font-smoothing: antialiased; text-rendering: optimizeLegibility; }
:root { --bg:#ffffff; --fg:#111213; --muted:#5b616e; --primary:#c6a30c; --primary-ink:#000; --accent:#c6a30c; --surface:#f6f6f6; --border:#e6e8ef; --radius:12px; --shadow:0 10px 30px rgba(0,0,0,.06); }
@media (prefers-color-scheme: dark){ :root{ --bg:#0f1115; --fg:#edf2ff; --muted:#a7b0c0; --primary:#d3b316; --primary-ink:#0b0d12; --accent:#d3b316; --surface:#151923; --border:#2a2f3a; --shadow:0 10px 30px rgba(0,0,0,.4);}}
.container{ width:min(1100px,92%); margin-inline:auto;} .section{ padding:64px 0;} .section.alt{ background:var(--surface);} .lead{ font-size:clamp(1.1rem,1.2vw + 1rem,1.35rem); color:var(--muted);} .grid{ display:grid; gap:24px;} .grid.cols-2{ grid-template-columns:1fr;} @media(min-width:900px){ .grid.cols-2{ grid-template-columns:repeat(2,1fr);}}
.grid.features{ grid-template-columns:repeat(auto-fit, minmax(220px,1fr)); } .checklist{ padding-left:1.2em;} .checklist li{ list-style:'✔  '; margin:.25rem 0;}
.btn{ display:inline-block; padding:12px 18px; border-radius:calc(var(--radius) - 4px); border:1px solid transparent; background:var(--primary); color:var(--primary-ink); font-weight:600; text-decoration:none; box-shadow:var(--shadow); transition:transform .06s ease, box-shadow .2s ease, background .2s ease;} .btn:hover{ transform:translateY(-1px);} .btn:active{ transform:translateY(0);} .btn-ghost{ background:transparent; border-color:var(--border); color:var(--fg); box-shadow:none;} .btn-sm{ padding:8px 12px; font-size:.95rem;}
.skip-link{ position:absolute; left:-9999px; top:auto; width:1px; height:1px; overflow:hidden;} .skip-link:focus{ position:fixed; left:16px; top:16px; width:auto; height:auto; background:var(--fg); color:var(--bg); padding:8px 12px; border-radius:8px; z-index:1000;}
.site-header{ position:sticky; top:0; background:#000; border-bottom:1px solid var(--border); z-index:100;} .header-inner{ display:flex; align-items:center; gap:18px; padding:14px 0;} .brand{ display:inline-flex; align-items:center; gap:10px; font-weight:800; text-decoration:none; color:#fff;} .brand img{ max-height:48px;} .brand .accent{ color:var(--primary);} .nav-toggle{ margin-left:auto; font-size:1.25rem; background:transparent; border:1px solid #333; border-radius:10px; padding:6px 10px; cursor:pointer; color:#fff;}
.nav{ display:none;} .nav ul{ display:grid; gap:10px; padding:0; margin:0; list-style:none;} .nav a{ color:#fff; text-decoration:none; padding:8px 10px; border-radius:8px;} .nav a:hover{ background:rgba(255,215,0,.12);} @media(min-width:900px){ .nav-toggle{ display:none;} .nav{ display:block; margin-left:auto;} .nav ul{ display:flex; align-items:center; gap:8px;}}
.hero{ padding:80px 0 56px;} .hero h1{ font-size:clamp(1.8rem,2.8vw + 1rem,3rem); line-height:1.1; margin:0 0 12px;} .hero .actions{ display:flex; gap:12px; flex-wrap:wrap;}
.features li{ background:var(--bg); border:1px solid var(--border); border-radius:var(--radius); padding:18px; box-shadow:var(--shadow);}
.form{ display:grid; gap:16px; max-width:640px;} .form-field{ display:grid; gap:8px;} input,textarea{ width:100%; padding:12px 14px; border-radius:10px; border:1px solid var(--border); background:var(--bg); color:var(--fg);} input:focus,textarea:focus{ outline:2px solid color-mix(in srgb, var(--primary) 40%, transparent); border-color:var(--primary);} .error{ color:#c0392b; min-height:1.2em;} .form-msg{ margin-top:6px; color:var(--muted);}
.contact-info p{ margin:6px 0;} .contact-info a{ color:var(--fg); text-decoration:none;} .contact-info a:hover{ color:var(--primary); text-decoration:underline;}
.site-footer{ border-top:1px solid var(--border); padding:32px 0; font-size:.95rem; color:var(--muted);} .site-footer nav{ display:flex; gap:16px; flex-wrap:wrap;} .site-footer nav a{ color:var(--muted); text-decoration:none;} .site-footer nav a:hover{ color:var(--primary);}
.sr-only{ position:absolute; width:1px; height:1px; margin:-1px; overflow:hidden; clip:rect(0 0 0 0); border:0;}
@media print{ .nav-toggle, .nav, .hero .actions, .form button{ display:none !important;} a{ text-decoration:none; color:black;}}


/* === Kontakt: większa widoczność + przesunięcie w prawo na desktopie === */
.contact-info {
  background: var(--bg);
  border: 1px solid var(--border);
  border-radius: var(--radius);
  padding: clamp(18px, 2vw, 28px);
  box-shadow: var(--shadow);
  font-size: clamp(1rem, 0.6vw + 1rem, 1.25rem);
  line-height: 1.8;
}
.contact-info p { margin: 8px 0; }
.contact-info strong { display: inline-block; margin-top: 8px; }
.contact-info a { color: var(--fg); text-decoration: none; font-weight: 700; }
.contact-info a:hover { color: var(--primary); text-decoration: underline; }

@media (min-width: 900px) {
  .contact-info {
    justify-self: end;      /* przesuń panel w prawo w siatce */
    max-width: 520px;
    margin-left: 24px;      /* odsuń od formularza */
  }
}

/* === Ulepszenia RWD i dostępności dotykowej === */
img { max-width: 100%; height: auto; }
.btn { min-height: 44px; } /* zalecany rozmiar tap target */
a[href^="tel:"], a[href^="mailto:"] { -webkit-touch-callout: default; }

/* Większe odstępy i czytelność na małych ekranach */
@media (max-width: 480px) {
  .header-inner { padding: 12px 0; }
  .section { padding: 48px 0; }
  .form { gap: 12px; }
  .btn { width: 100%; text-align: center; }
}

/* HERO with background image and vignette */
.hero.hero-bg{
  position:relative;
  color:#fff;;
  background-size:cover;
  background-position:center;
  background-repeat:no-repeat;
}
.hero.hero-bg h1{ color:#fff; }
.hero.hero-bg .lead{ color:#f0f3f8; }
.hero.hero-bg .btn-ghost{ color:#fff; border-color:rgba(255,255,255,.4);}
.hero.hero-bg .btn-ghost:hover{ background:rgba(255,255,255,.12);}

/* Kadrowanie hero dla różnych ekranów */
.hero.hero-bg {
  background-size: cover;
  background-position: center;
}

@media (max-width: 899px) {
  .hero.hero-bg {
    background-position: top;
  }
}

/* Kontakt w nawigacji jako złoty przycisk */
.nav .btn-nav{
  background: var(--primary);
  color: var(--primary-ink);
  padding: 8px 14px;
  border-radius: 8px;
  font-weight: 600;
}
.nav .btn-nav:hover{
  background: color-mix(in srgb, var(--primary) 85%, #000 15%);
}

/* Gallery */
.gallery{ list-style:none; padding:0; margin:0; display:grid; gap:12px; grid-template-columns:1fr; }
.gallery li{ overflow:hidden; border-radius:var(--radius); border:1px solid var(--border); background:var(--bg); }
.gallery img{ display:block; width:100%; height:auto; }
@media(min-width:600px){ .gallery{ grid-template-columns:repeat(2, 1fr); } }
@media(min-width:1000px){ .gallery{ grid-template-columns:repeat(3, 1fr); } }
.hint{ color:var(--muted); font-size:.95rem; margin-top:12px; }
/* Lightbox */
.lightbox[hidden]{ display:none; }
.lightbox{ position:fixed; inset:0; background:rgba(0,0,0,.85); display:flex; align-items:center; justify-content:center; padding:24px; z-index:1000; }
.lb-img{ max-width:90vw; max-height:85vh; border-radius:10px; box-shadow:var(--shadow); }
.lb-close{ position:absolute; top:16px; right:16px; font-size:32px; line-height:1; background:transparent; color:#fff; border:1px solid rgba(255,255,255,.4); border-radius:10px; width:44px; height:44px; cursor:pointer; }


.features li {
  list-style-type: none;
}